.projects {
  display: flex;
  justify-content: center;
  align-items: center;
}
.slide-container {
  height: 50vh;
  width: 50vw;
}

.project {
  height: 50vh;
  width: 50vw;
  border-radius: 20px;
}

.project-sections {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
}

.links {
  display: flex;
  justify-content: space-evenly !important;
}

.carousel-caption {
  color: #0e3254;
  font-weight: bolder;
  z-index: 0 !important;
}

.carousel-caption > h5 {
  font-weight: 500;
}

.links > div > a {
  color: #0e3254;
}

.links > div > a:hover {
  color: rgb(36, 88, 192);
}

.carousel-control-next-icon {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%230e3254;'%3E%3Cpath d='M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z'/%3E%3C/svg%3E") !important;
}

.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%230e3254;'%3E%3Cpath d='M11.354 1.646a.5.5 0 0 1 0 .708L5.707 8l5.647 5.646a.5.5 0 0 1-.708.708l-6-6a.5.5 0 0 1 0-.708l6-6a.5.5 0 0 1 .708 0z'/%3E%3C/svg%3E") !important;
}

.carousel .carousel-control-prev-icon, .carousel .carousel-control-next-icon {
    width: 45px;
    height: 45px;
}




.carousel-caption {
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(5px);
}

.carousel-indicators li {
  background-color: #0e3254;
}

.carousel-indicators li:focus {
    background-color: #000000;
  }

.carousel .carousel-indicators li {
  width: 1.55rem;
  border-radius: 10%;
}

@media screen and (max-width: 16395px) {
  .slide-container,
  .project {
    width: 60vw;
  }
}

@media screen and (max-width: 1213px) {
  .slide-container,
  .project {
    width: 70vw;
  }
}

@media screen and (max-width: 967px) {
  .slide-container,
  .project {
    width: 80vw;
  }
}

@media screen and (max-width: 629px) {
  .slide-container,
  .project {
    width: 90vw;
    height:45vh;
  }
  .carousel-caption > h5 {
    font-size: 1.0rem;
  }
  .carousel-caption > p, .links > div > a {
    font-size: 0.8rem;
  }
}

@media screen and (max-height: 586px) {
  .project-sections {
    margin-top: 20rem;
    margin-bottom: 10rem;
  }
}

@media screen and (max-height: 400px) {
  .project-sections {
    margin-top: 30rem;
    margin-bottom: 10rem;
  }
}

@media screen and (max-height: 293px) {
  .project-sections {
    margin-top: 40rem;
    margin-bottom: 10rem;
  }
}